@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.header{display:flex;justify-content:center;background-color:#BBD1DE;padding:2em;box-shadow:2px 2px 7px rgba(36,36,36,0.288)}.header img{max-width:96px;height:40px}.header h1{font-family:"Roboto", sans-serif;font-size:2.5em;padding-left:.3em}@media screen and (max-width: 500px){.header h1{font-size:1.8rem}}@media screen and (max-width: 360px){.header h1{font-size:1.5rem}}.footer{text-align:center;background-color:#BBD1DE;padding:10px 0}.form{position:relative;margin:0 0 15px 9px}.form label{position:absolute;left:0;top:15px;z-index:-1;color:#CDC1A9;font-size:.9rem;transition:.2s ease-in-out}@media screen and (max-width: 400px){.form label{font-size:.85rem}}.form input{margin:17px 19px 0 0;padding:0px 0 3px 0px;color:#CDC1A9;background-color:rgba(42,42,42,0.87);background:transparent;border-bottom:1px solid #CDC1A9}.form input:focus+label,.form input:valid ~ label{top:-5px;font-size:.75rem}.form input:-webkit-autofill,.form input:-webkit-autofill:focus{-webkit-text-fill-color:#CDC1A9;-webkit-transition:background-color 5000s ease-in-out 0s;transition:background-color 5000s ease-in-out 0s}.form button{cursor:pointer;padding:5px 10px;letter-spacing:.05rem;border-radius:6px;background-color:#CDC1A9;transition:-webkit-transform .3s;transition:transform .3s;transition:transform .3s, -webkit-transform .3s}@media screen and (max-width: 350px){.form button{margin-top:5px !important;font-size:.7rem !important}}.form button:hover{-webkit-transform:scaleX(1.1);transform:scaleX(1.1)}.message__alert{font-size:.9rem;padding:2px 8px;border-radius:4px;color:white;margin-bottom:12px;background-color:red}@media screen and (max-width: 400px){.message__alert{font-size:.8rem}}.message__alert--success{background-color:#4aaf58}.message__alert--error{background-color:#f13232}li{cursor:pointer;font-size:0.9rem;color:white;transition:.3s ease-in-out}@media screen and (max-width: 850px){li{font-size:0.8rem}}li:hover{color:#CDC1A9;-webkit-transform:translateX(-1px);transform:translateX(-1px)}.main{max-width:90vw;margin:1rem auto;border-radius:.6rem;box-shadow:2px 2px 2px rgba(10,10,10,0.089);background-image:url(/static/media/antique-bg.307a35d9.jpg);background-size:cover;background-position:center;min-height:80vh}.main__content{position:absolute;left:50%;top:50%;min-height:250px;background-color:rgba(42,42,42,0.87);-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);border-radius:10px;padding:15px 30px}@media screen and (max-width: 850px){.main__content{width:55%}}@media screen and (max-width: 620px){.main__content{width:65%}}@media screen and (max-width: 550px){.main__content{width:80%}}@media screen and (max-width: 450px){.main__content{width:95%}}.main__content h1{letter-spacing:.1rem;text-shadow:-3px -3px 14px #bbd1de;color:#CDC1A9;font-size:1.5rem;margin-bottom:17px;text-align:center}.main__content__members{-webkit-columns:3 auto;columns:3 auto;margin-top:5px;padding:5px;text-align:center}@media screen and (max-width: 400px){.main__content__members{-webkit-columns:2 auto;columns:2 auto}}*{margin:0;padding:0;box-sizing:border-box;list-style-type:none;outline:none;border:none;font-family:"Roboto", sans-serif;text-decoration:none}body{background-color:#CDC1A9}

